.card {
  background-color: $white;
  font-size: $homePage;
  width: 16.5rem;
  border-radius: 0.5rem;
  overflow: hidden;
  font-weight: $light;
  box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.05);
  color: $veryVeryDarkBlue;
  cursor: pointer;
  a, a:-webkit-any-link, a:visited {
    text-decoration: none;
    outline: none;
    border: none;
    color:$veryVeryDarkBlue;
  }
}

.dark-mode {
  background-color: $darkBlue;
  color: $white;
}
.country-flag {
  width: 100%;
  height: 11rem;
}
.card-info {
  padding-block: 1.5rem 2rem;
  padding-inline: 1.7rem;
  font-size: 1rem;
}
.country-name {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
}

.country-data {
  font-size: 0.95rem;
  display: block;
  margin-bottom: 0.5rem;
}
.country-data_label {
  margin-right: 0.3rem;
}
